html, body{
    margin:0;
    padding:0;
    height: 100%;
    width:100%;
    overflow-x: hidden;
    /* font-family: 'Source Code Pro', monospace; */
    font-family: Menlo, Monaco, "Consolas", "Courier New", "Courier";
}

.modal_container {
      z-index: 8000;
      top: 50%;
      left: 0px;
      right: 0px;
      width: 100%;
      margin: auto;
      width: 100%;
      -webkit-transform: translate3d(0px, 0px, 0px);
              transform: translate3d(0px, 0px, 0px);
      outline: 0;
      pointer-events: auto;
      -webkit-transition: all .2s cubic-bezier(0.770, 0.000, 0.175, 1.000);
      -o-transition: all .2s cubic-bezier(0.770, 0.000, 0.175, 1.000);
      transition: all .2s cubic-bezier(0.770, 0.000, 0.175, 1.000);
  }

  .modal_container_simple {
    max-width: calc(44.625em + 1rem);
  }

  .modal_light {
      background-color: #fff;
      -webkit-box-shadow: 0 .625em 1.25em rgba(0,0,0,.25);
              box-shadow: 0 .625em 1.25em rgba(0,0,0,.25);
  }

  .modal_simple {
      border-radius: 7px;
  }

  .modal_content {
      z-index: 1;
      display: -ms-flexbox;
      display: flex;
      -ms-flex-direction: column;
          flex-direction: column;
      width: 100%;
      position: relative;
  }

  .modal_header, .modal_body, .modal_footer {
      display: -ms-flexbox;
      display: flex;
  }

  .modal_header {
      -ms-flex-pack: center;
          justify-content: center;
      position: relative;
      -ms-flex-direction: column;
          flex-direction: column;
      -ms-flex-align: center;
          align-items: center;
  }

  .modal_header_custom {
    padding: 40px 0px 60px 0px;
  }

  .modal_header_decor {
    display: -ms-flexbox;
    display: flex;
    height: 1px;
    width: 100%;
    background-color: rgba(0,0,0,.25);
    /* border-top: 1px solid rgba(0,0,0,.25); */
    /* border-bottom: 1px solid rgba(0,0,0,.25); */
  }

  .modal_header_img_container {
    border: 1px solid rgba(0,0,0,.25);
    height: 64px;
    width: 64px;
    background-color: #fff;
    border-radius: 50%;
    /* box-shadow: 0 0 0 4px #fff, inset 0 2px 0 rgba(0,0,0,.25), 0 3px 0 4px rgba(0,0,0,.25); */
    position: absolute;
  }

  .modal_header_img {
    padding-left: 11px;
    padding-top: 11px;
  }

  .modal_title {
      font-size: 45px;
      /* color: #fff; */
      white-space: nowrap;
      margin: 12px;
      letter-spacing: 2px;
  }

  .modal_x-close {
      cursor: pointer;
      width: 25px;
      margin: 10px 5px -10px auto;
  }

  .modal_body {
      -ms-flex-align: center;
          align-items: center;
      -ms-flex: 1 1 auto;
          flex: 1 1 auto;
      position: relative;
      -ms-flex-direction: column;
          flex-direction: column;
      padding: 15px 10px;
      margin-top: -20px;
      margin-bottom: 20px;
  }

  .modal_body_description {
    max-width: 600px;
    text-align: center;
    line-height: 30px;
  }

  .modal_body_description p{
    margin: 0;
    margin-bottom: 10px;
    /* color: #fff; */
  }

  .modal_footer {
      /* color: #fff; */
      -ms-flex-pack: justify;
          justify-content: space-between;
      -ms-flex-align: center;
          align-items: center;
      position: relative;
      border-top: 1px solid rgba(255,255,255,0.3);
      padding: 10px;
  }

  .modal_footer .footer-left {
    padding: 5px 10px;
  }

  .modal_footer .footer-right {
    padding: 5px 10px;
  }

.c_overlay {
  position: fixed;
  z-index: 7000;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  min-width: 100%;
  min-height: 100%;
  -ms-flex-align: center;
      align-items: center;
  overflow: auto;
  outline: 0;
  -webkit-overflow-scrolling: touch;
}

.c_overlay_dark {
  background-color: rgba(0,0,0,.5);
}

.ReactModal__Body--open  {
  -webkit-transition: all .2s cubic-bezier(0.770, 0.000, 0.175, 1.000);
  -o-transition: all .2s cubic-bezier(0.770, 0.000, 0.175, 1.000);
  transition: all .2s cubic-bezier(0.770, 0.000, 0.175, 1.000); 
}

.ReactModal__Body--open  #root{
  -webkit-filter: blur(3px);
          filter: blur(3px);
}

.ReactModal__Content {
  -webkit-transition: all 200ms cubic-bezier(0.165, 0.84, 0.44, 1);
  -o-transition: all 200ms cubic-bezier(0.165, 0.84, 0.44, 1);
  transition: all 200ms cubic-bezier(0.165, 0.84, 0.44, 1);
  -webkit-transform: translateY(8px);
      -ms-transform: translateY(8px);
          transform: translateY(8px);
  opacity: 0;
}
.ReactModal__Content--after-open {
    opacity: 1;
    -webkit-transform: translateY(0);
        -ms-transform: translateY(0);
            transform: translateY(0);
}
.ReactModal__Content--before-close {
    opacity: 0;
    -webkit-transform: translateY(8px);
        -ms-transform: translateY(8px);
            transform: translateY(8px);
}



