.terminal-image-window {
    margin: 0 auto;
    width: 200px;
    height: 150px;
    text-align: left;
    margin-top: 15px;
    position: relative;
    border-radius: 10px;
    border: 1.5px solid black;
    margin-bottom: 30px;
  }
  
  .terminal-image-window header {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-line-pack: center;
        align-content: center;
    -ms-flex-pack: justify;
        justify-content: space-between;
    background-color: #E0E8F0;
    height: 30px;
    border-radius: 8px 8px 0 0;
    padding-left: 10px;
    border-bottom: 1px solid black;
  }

  .terminal-name {
      color: #0f0f0f;
      margin-top: 7px;
      margin-left: -55px;
  }

  .terminal-center {
      display: -ms-flexbox;
      display: flex;
  }
  
  .terminal-image-window .button {
    width: 12px;
    height: 12px;
    margin: 10px 4px 0 0;
    display: inline-block;
    border-radius: 8px;
  }
  
  .red_btn {
    background-color: #E75448;
  }
  
  .green_btn {
    background-color: #3BB662;
  }
  
  .yellow_btn {
    background-color: #E5C30F;
  }
  
  .terminal-image-window {
    /* background-color: #0D1F2D; */
    background-color: rgba(255, 255, 255, 0.8);
    color: #F4FAFF;
    font-size: 11pt;
  }

  .terminal-image-name {
    color: #0f0f0f;
    margin-top: 7px;
    margin-left: -35px;
  }

  .terminal-image-container {
      display: -ms-flexbox;
      display: flex;
      -ms-flex-pack: center;
          justify-content: center;
      margin-top: 30px;
  }

  .terminal-image-container.no-top-margin{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    margin-top: 0px;
  }

  .terminal-image-window:hover {
    cursor: pointer;
    background-color: rgba(255, 255, 255, 0.5);
}

.terminal-portfolio {
  width: 400px;
  height: 250px;
}

.react-dash-img {
  height: 212px;
  width: 400px;
}

@media screen and (max-width: 764px) {
  .project-row .terminal-portfolio {
    width: 300px;
    height: 200px;
  }
  .react-dash-img {
    height: 163px;
    width: 301px;
  }
}


